@charset "UTF-8";
/* ----------------------------- */
/* ==Base (basic styles)         */
/* ----------------------------- */
/* switching to border-box model for all elements */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit;
  /* avoid min-width: auto on flex and grid children */
  min-width: 0;
  min-height: 0; }

html {
  /* set base font-size to equiv "10px", which is adapted to rem unit */
  font-size: 62.5%;
  /* IE9-IE11 math fixing. See http://bit.ly/1g4X0bX */
  font-size: calc(1em * 0.625); }

body {
  margin: 0;
  font-size: 1.4rem;
  background-color: #fff;
  color: #212529;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  line-height: 1.5; }
  @media (min-width: 576px) {
    body {
      font-size: 1.6rem; } }

/* Links */
a {
  color: #333;
  text-decoration: underline; }
  a:focus, a:hover, a:active {
    color: #0d0d0d;
    text-decoration: underline; }

/* Headings */
h1, .h1-like {
  font-size: 2.8rem;
  font-family: sans-serif;
  font-weight: 500; }
  @media (min-width: 576px) {
    h1, .h1-like {
      font-size: 3.2rem; } }

h2, .h2-like {
  font-size: 2.4rem;
  font-family: sans-serif;
  font-weight: 500; }
  @media (min-width: 576px) {
    h2, .h2-like {
      font-size: 2.8rem; } }

h3, .h3-like {
  font-size: 2rem;
  font-weight: 500; }
  @media (min-width: 576px) {
    h3, .h3-like {
      font-size: 2.4rem; } }

h4, .h4-like {
  font-size: 1.8rem;
  font-weight: 500; }
  @media (min-width: 576px) {
    h4, .h4-like {
      font-size: 2rem; } }

h5, .h5-like {
  font-size: 1.6rem;
  font-weight: 500; }
  @media (min-width: 576px) {
    h5, .h5-like {
      font-size: 1.8rem; } }

h6, .h6-like {
  font-size: 1.4rem;
  font-weight: 500; }
  @media (min-width: 576px) {
    h6, .h6-like {
      font-size: 1.6rem; } }

/* Vertical rythm */
h1,
h2,
h3,
h4,
h5,
h6,
dd {
  margin-top: 0;
  margin-bottom: 0.5rem; }

p,
address,
ol,
ul,
dl,
blockquote,
pre {
  margin-top: 0;
  margin-bottom: 1rem; }

/* Avoid margins on nested elements */
li p,
li .p-like,
li ul,
li ol,
ol ol,
ul ul {
  margin-top: 0;
  margin-bottom: 0; }

/* Max values */
img,
table,
td,
blockquote,
code,
pre,
textarea,
input,
video,
svg {
  max-width: 100%; }

img {
  height: auto; }

/* Styling elements */
ul,
ol {
  padding-left: 2em; }

img {
  vertical-align: middle; }

em,
.italic,
address,
cite,
i,
var {
  font-style: italic; }

code,
kbd,
mark {
  border-radius: 2px; }

kbd {
  padding: 0 2px;
  border: 1px solid #999; }

pre {
  tab-size: 2; }

code {
  padding: 2px 4px;
  background: rgba(0, 0, 0, 0.04);
  color: #b11; }

pre code {
  padding: 0;
  background: none;
  color: inherit;
  border-radius: 0; }

mark {
  padding: 2px 4px; }

sup,
sub {
  vertical-align: 0; }

sup {
  bottom: 1ex; }

sub {
  top: 0.5ex; }

blockquote {
  position: relative;
  padding-left: 3em;
  min-height: 2em; }

blockquote::before {
  content: "\201C";
  position: absolute;
  left: 0;
  top: 0;
  font-family: georgia, serif;
  font-size: 5em;
  height: .4em;
  line-height: .9;
  color: #e7e9ed; }

blockquote > footer {
  margin-top: .75em;
  font-size: 0.9em;
  color: rgba(0, 0, 0, 0.7); }
  blockquote > footer::before {
    content: "\2014 \0020"; }

q {
  font-style: normal; }

q,
.q {
  quotes: "“" "”" "‘" "’"; }
  q:lang(fr),
  .q:lang(fr) {
    quotes: "«\00a0" "\00a0»" "“" "”"; }

hr {
  display: block;
  clear: both;
  height: 1px;
  margin: 1em 0 2em;
  padding: 0;
  border: 0;
  color: #ccc;
  background-color: #ccc; }

blockquote,
figure {
  margin-left: 0;
  margin-right: 0; }

code,
pre,
samp,
kbd {
  white-space: pre-wrap;
  font-family: consolas, courier, monospace;
  line-height: normal; }

html, body {
  height: 100%;
  padding: 0; }

.app-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap; }
  .app-wrapper > header,
  .app-wrapper > footer {
    flex-shrink: 0; }
  .app-wrapper > main {
    flex-grow: 1;
    display: flex; }
    .app-wrapper > main .app-side-bar {
      flex: 0 1 20%;
      overflow-y: auto; }
    .app-wrapper > main .app-content {
      flex: 0 1 auto;
      width: 100%;
      overflow-y: auto; }

header {
  display: flex;
  padding: 1rem 2rem;
  background-color: #0074D9;
  color: rgba(255, 255, 255, 0.8); }

.app-side-bar {
  background-color: #2ECC40;
  color: rgba(0, 0, 0, 0.8);
  padding: 1rem; }

.app-footer {
  padding: 0.5rem 1rem;
  font-size: 0.8rem;
  color: #001F3F;
  background-color: #DDDDDD;
  border-top: 1px solid #AAAAAA; }
  @media (min-width: 576px) {
    .app-footer {
      font-size: 1rem; } }

.editor-area {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100%; }
  .editor-area .left, .editor-area .right {
    flex: 0 1 50%; }
  .editor-area .left #source {
    background-color: white;
    font-family: consolas, courier, monospace;
    font-size: 1.2rem;
    padding: 10px;
    height: 100%; }
    @media (min-width: 576px) {
      .editor-area .left #source {
        font-size: 1.4rem; } }
    .editor-area .left #source p {
      margin: 0; }
  .editor-area .right #preview {
    background-color: #DDDDDD;
    font-family: monospace;
    font-size: 14px;
    padding: 10px;
    height: 100%; }
    .editor-area .right #preview p {
      margin: 0; }

.file-explorer {
  font-size: 1.2rem; }
  @media (min-width: 576px) {
    .file-explorer {
      font-size: 1.4rem; } }
  .file-explorer .entry-list {
    padding-left: 0;
    list-style: none; }
    .file-explorer .entry-list .entry {
      cursor: pointer;
      padding: 0.5rem; }
      .file-explorer .entry-list .entry:hover {
        background-color: rgba(255, 255, 255, 0.3);
        color: #111111; }
      .file-explorer .entry-list .entry.active {
        background-color: rgba(255, 255, 255, 0.6);
        color: #111111; }
      .file-explorer .entry-list .entry [contenteditable="true"] {
        background-color: #FFFFFF;
        cursor: text;
        color: #111111; }
      .file-explorer .entry-list .entry .entry-remover {
        color: #AAAAAA;
        font-size: 0.8rem;
        cursor: pointer; }
        @media (min-width: 576px) {
          .file-explorer .entry-list .entry .entry-remover {
            font-size: 1rem; } }
        .file-explorer .entry-list .entry .entry-remover:hover {
          color: #FF4136; }

.rendering-options {
  text-align: right;
  flex: 1 1 auto;
  font-size: 1.2rem;
  display: flex;
  justify-content: flex-end; }
  @media (min-width: 576px) {
    .rendering-options {
      font-size: 1.4rem; } }
  .rendering-options .panel {
    flex: 0 1 auto; }
